<template>
	<view class="container" style="width: 750rpx;height: 500rpx;">

		<video class="vp-v" :src="src" autoplay style="width: 100%;height: 250rpx;"></video>
	</view>	
</template>

<script>
	import request from "@/units/request.js"
	import polyv from "@/units/polyv.js"
	import utilMd5 from "@/units/md5.js"
	export default {
		data() {
			return {
				homeSlide: [],
				artClassesNum: 0, //瑞晟艺术课程单一高亮显示和显示对应的list
				courseTypeData: [], //一级导航
				courseLibs: "", //首页课程列表数据

				titieLibs: [{ //成人课程的二级导航
						name: "全部"
					},
					{
						name: "音乐素养"
					},
					{
						name: "声乐演唱"
					},
					{
						name: "钢琴演奏"
					},
					{
						name: "葫芦丝演奏"
					},
					{
						name: "音乐器材"
					},
				],
				adultHightText: '全部',
				teaHightText: '全部',
				teaHightLight: '0', //明星讲师的二级导航高亮
				adultHightLight: '0', //成人课程的二级导航高亮
				lecturerList: '', //明星讲师
				singleOpenid: '', //
				// menuList:[],//一级菜单跟二级菜单

				src: '', //视频播放地址
			}
		},
		onReady() {
			// uni.showShareMenu()
			// this.getmenuList() //获取一级菜单跟二级菜单
			// this.getSlicePic() //获取轮播图图片
			// 未开发提示
			// uni.showToast({
			// 	icon: 'none',
			// 	title: '敬请期待',
			// 	duration: 900000
			// });

			

			// ----------------

			let vid = 'e91629260cd1aa799c6cceba700bff0d_e';
			let timestamp = Date.parse(new Date());
			let secretKey = "PXW1ldTOqD"; //
			let ts = timestamp;
			let sign = utilMd5.hexMD5(secretKey + vid + ts);
			/*获取视频数据*/
			let obj = {
				vid: vid,
				ts: ts,
				sign: sign,
				callback: videoInfo => {
					if (videoInfo.type === 'error') {
						console.log('videoInfo', videoInfo);
						return;
					}
					this.src= videoInfo.src[0]
				}
			};

			this.player = polyv.getVideo(obj);
			// ------------
		},


		onUnload: function() {
			this.player.destroy();
		},
		methods: {

			// 获取一级菜单跟二级菜单
			getmenuList() {
				let _self = this
				uni.request({
					url: _self.$url + 'classtype/netlist',
					success(res) {
						for (var i = 0; i < res.data.content.length; i++) {
							res.data.content[i].typeImg = _self.$url + res.data.content[i].typeImg
							if (res.data.content[i].children.length > 1) {
								var lib = {
									typeName: "全部",
									parentId: res.data.content[i].id
								}
								res.data.content[i].children.unshift(lib)
							}
						}
						//  console.log('菜单', res)
						_self.courseTypeData = res.data.content
						_self.toCompany(res.data.content[0])
					}
				})
			},


			//点击一级导航，请求对应的一级数据
			toCompany(item, index) {
				uni.showLoading({
					title: ''
				});
				this.artClassesNum = index
				let _self = this
				uni.request({
					url: _self.$url + item.typeUri + "?type=" + item.id,
					method: 'GET',
					success(res) {
						uni.hideLoading();
						for (var i = 0; i < res.data.content.length; i++) {
							res.data.content[i].picture = _self.$url + res.data.content[i].picture
						}
						if (item.typeUri.indexOf('teacher') > 0) { //请求明星老师的数据
							_self.lecturerList = res.data.content
						} else if (item.typeUri.indexOf('class') > 0) {
							_self.courseLibs = res.data.content
						}

						_self.teaHightLight = 0 //明星讲师的二级导航高亮
						_self.teaHightText = '全部'
						_self.adultHightLight = 0 //成人课程的二级导航高亮
						_self.adultHightText = '全部'
					}
				})
			},



			// 点击二级导航，进行高亮显示并请求数据
			changeLight(item, index) {
				var num = ''
				for (var k = 0; k < this.courseTypeData.length; k++) {
					if (this.courseTypeData[k].id == item.parentId) {
						num = k
					}
				}

				let _self = this //uni.login中this指向会出错，需重指向
				if (index == 0) {
					_self.toCompany(_self.courseTypeData[num]) //点击全部的时候获取全部数据
				} else {
					uni.request({
						url: _self.$url + _self.courseTypeData[num].typeUri + "?type=" + _self.courseTypeData[num].id + '&subType=' +
							item.id,
						success(res) {
							for (var i = 0; i < res.data.content.length; i++) {
								res.data.content[i].picture = _self.$url + res.data.content[i].picture
							}
							if (num == 0) {
								_self.teaHightLight = index
								_self.lecturerList = res.data.content
								_self.teaHightText = item.typeName
							} else if (num == 1) {
								_self.adultHightText = item.typeName
								_self.adultHightLight = index
								_self.courseLibs = res.data.content
							}

						}
					})
				}
			},



			//点击成人课程中的商品进入对应的商品详情页面
			toDetailPage(item) {
				this.$store.state.videoItem = item
				uni.navigateTo({
					// url: "../details/details?item=" + encodeURIComponent(JSON.stringify(item))
					url: "../videoPage/videoPage"
				})
			},



			// 获取轮播图图片
			getSlicePic() {
				let _self = this
				uni.request({
					url: _self.$url + '/banner/netlist',
					success: (res) => {
						for (var i = 0; i < res.data.content.length; i++) {
							res.data.content[i].imgUrl = _self.$url + res.data.content[i].imgUrl
						}
						this.homeSlide = res.data.content

					}
				})
			},
		},

	}
</script>

<style>
	/* .container {
		width: 100%;
		height: 100%;
	} */
	@import "course.css";
</style>
